* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.Explore-comp {
    height: 200vh;
}

.nav-top {
    width: 100%;
    height: fit-content;
    background-color: var(--Ground-Black);

    display: flex;
    flex-direction: column;
}

.nav-top > p {
    font-family: 'Main-Bold';
    font-size: 14px;
    color: white;
    padding: 16px 0px 0px 16px;
}

/* Image Slider */
.Image-slider {
    margin-top: 12px;
    height: fit-content;
    width: 100%;
}