* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

nav {
    gap: 8px;
    padding: 16px 16px;
    width: 100%;
    height: 54px;
    background-color: white;
    border-bottom: 1px solid var(--Line-universal);

    display: flex;
    flex-direction: row;
}

/* Toggle */
.toggle-switch {
    position: relative;
    display: inline-block;
    display: flex;
    flex-direction: row;
    gap: 8px;
  }

  .toggle-switch button {
    padding: 4px 14px;
    border: none;
    border-radius: 4px;
    color: white;
    font-family: inter;
    font-size: 10px;
    font-weight: 600;
    cursor: pointer;
    background-color: var(--Ground-Black);
  }
  
  .toggle-input {
    display: none;
  }
  
  .toggle-label {
    margin-top: 8px;
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 20px;
    border-radius: 17px;
    background-color: #ccc;
    cursor: pointer;
    transition: background-color 0.4s ease;
  }
  
  .toggle-label:before {
    content: '';
    position: absolute;
    top: 3px;
    left: 3px;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: transform 0.4s ease;
  }
  
  .toggle-input:checked + .toggle-label {
    background-color: #86d993;
  }
  
  .toggle-input:checked + .toggle-label:before {
    transform: translateX(20px);
  }

.SettingContainer {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 16px 16px;
}

.darkmode {
    height: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 12px;
    cursor: pointer;

}

.PopupTheme {
  position: absolute;
  width: 260px;
  height: 240px;
  background-color: var(--Ground-White);
  border-radius: 16px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}