* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Color library */
:root {
    --Btn-color: #ff6347;
}

/* Todo: Warna elemen */
:root {
    --Explore-resep: #FEDBCC;
    --Coock-book: #E8EAF9;
    --Info-gizi: #ECF5C5;
}

.New-recipe-sect {
    min-width: 328px;
    /* margin-top: 32px; */
    margin-top: 24px;
    height: 247px;
}

.Wrappper-new-sect {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.Top-new-sect {
    margin: 0px 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Top-new-sect div:nth-child(1) {
    user-select: none;
    display: flex;
    flex-direction: column;
    gap: 6px;
}

.Top-new-sect h1 {
    font-size: 16px;
    font-family: 'Main-Bold', sans-serif;
}

.Top-new-sect p {
    font-size: 12px;
    font-family: 'Main-Book', sans-serif;
    color: var(--SubHeading-shade);
}

/* Button section */
.Top-new-sect div:nth-child(2) button {
    width: 80px;
    height: 25px;
    border-radius: 4px;
    border: none;
    font-size: 12px;
    font-family: 'Main-Bold', sans-serif;
    color: var(--Btn-color);
    background-color: var(--Background-cta-btn);
    margin-top: 12px;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;
}

.Top-new-sect div:nth-child(2) button img {
    border-left: 1px solid white;
    padding-left: 8px;
}

/* Content Sect */
.Content-new-sect {
    margin: 12px 16px;
}

.wrapper-content-newSect {
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    gap: 8px;
    padding-bottom: 12px;
    font-family: 'Main-Bold', sans-serif;
}

/* Hide Scrollbar but overflow stay func */
.wrapper-content-newSect::-webkit-scrollbar {
    display: none;
}

/* Popup props */
.PropsOpen {
    /* width: 360px; */
    width: 100vw;
    height: 437px;
    bottom: 40px;
    position: fixed;
    background-Color: white;
    z-Index: 30;
    border-radius: 20px 20px 0px 0px;
}
/* Props style */
.Wrapper-propsOpen {
    display: flex;
    flex-direction: column;
    padding: 0px 16px;
}

.HandleContainer {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 32px;
}

.HandleToggle {
    width: 30px;
    height: 4px;
    background-Color: #8B8B8B99;
    border-Radius: 20px;
    margin: 12px auto;
    cursor: pointer;
}

/* Image props */
.Image-props {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Info props */
.info-props {
    margin-top: 12px;
    display: flex;
    flex-direction: column;
}

.info-props h1 {
    font-size: 16px;
    font-weight: 600;
    font-family: 'Main-Bold', sans-serif;
}

.info-props p {
    margin-top: 4px;
    font-size: 16px;
    font-size: 12px;
    font-weight: 500;
    font-family: 'inter';
}

/* Info step */
.InfoStep {
    width: 100%;
    height: fit-content;
    margin-top: 24px;

    display: flex;
    flex-direction: column;
}

.InfoStep div {
    display: flex;
    flex-direction: row;
    gap: 4px;
    align-items: center;
}

.InfoStep div div:nth-child(1) p {
    font-size: 12px;
    font-weight: 400;
    font-family: "inter", sans-serif;
}

.InfoStep div div:nth-child(2) p {
    margin-left: -2px;
    font-size: 12px;
    font-weight: 400;
    font-family: "inter", sans-serif;
}

/* Cta btn */
.Cta-btn-props {
    margin-top: 24px;
    width: 100%;
    height: fit-content;
}

.Wrapper-cta-btn-props {
    width: 100%;
    height: 100%;
    gap: 8px;

    display: flex;
    flex-direction: row;
}

.Start-coock-props {
    width: 265px;
    height: 40px;
    background-color: var(--Btn-color);
    border-radius: 8px;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;
}

.Start-coock-props p {
    font-size: 14px;
    font-weight: 500;
    color: white;
    font-family: "inter", sans-serif;
}

/* Save btn */
:root {
    --Shade-color: #E6E6E6;
}

.Save-coock-props {
    width: 50px;
    height: 40px;
    background-color: var(--Shade-color);
    color: var(--Btn-color);
    font-size: 16px;
    border-radius: 8px;

    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.notif-saved {
    width: fit-content;
    height: 50px;
    background-color: black;
    display: flex;
    align-items: center;

    position: fixed;
    top: 60px;
    z-index: 200;
    color: white;
}