
/* HOME CTA */
/* Todo: Warna elemen */
:root {
    --Explore-resep: #FEDBCC;
    --Coock-book: #E8EAF9;
    --Info-gizi: #ECF5C5;
  }
  
  .Home-cta {
    border-radius: 16px;
    margin: 24px 16px 32px 16px;
    min-width: 328px;
    height: 120px;
    height: 120px;
    box-shadow: 0px 4px 39px rgba(0, 0, 0, 0.144);
  }
  
  .Wrapper-Home-cta {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between ;
    align-items: center;
    padding: 32px 36px;
  }
  
  /* CTA 01 - Explore resep */
  .Explore-resep {
    width: 48px;
    height: 48px;
    /* background-color: var(--Gray-backColor); */
    /* background-color: var(--Explore-resep); */
    border-radius: 8px;
  
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* .Wrapper-Home-cta div:nth-child(1) = .Explore-resep */
  .Wrapper-Home-cta div:nth-child(1) {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
  }
  .Wrapper-Home-cta div:nth-child(1) p {
    font-family: 'Main-Book';
    font-size: 12px;
  }
  
  /* CTA 02 - Cook book */
  .Coock-book {
    width: 48px;
    height: 48px;
    /* background-color: var(--Coock-book); */
    background-color: var(--Gray-backColor);

    border-radius: 8px;
  
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* .Wrapper-Home-cta div:nth-child(2) = .Coock-book */
  .Wrapper-Home-cta div:nth-child(2) {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
  }
  .Wrapper-Home-cta div:nth-child(2) p {
    font-family: 'Main-Book';
    font-size: 12px;
  }
  
  
  /* CTA 03 - Info gizi */
  .Info-gizi {
    width: 48px;
    height: 48px;
    background-color: var(--Gray-backColor);

    /* background-color: var(--Info-gizi); */
    border-radius: 8px;
  
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* .Wrapper-Home-cta div:nth-child(3) = .Info-gizi */
  .Wrapper-Home-cta div:nth-child(3) {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
  }
  .Wrapper-Home-cta div:nth-child(3) p {
    font-family: 'Main-Book';
    font-size: 12px;
  }
  
  .X-mark {
    position: relative;
    right: 12px;
    display: none;
    cursor: pointer;
  }