* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* Color Library */
:root {
    --SubHeading-shade: #666666;
    --Accent-color-btn: #FF6347;
}

:root {
    --Ground-White: #FCFCFC;
    --Ground-Black: #111111;
}

@font-face {
    font-family: "Main-Bold";
    src: url(/src/Assets/Fonts/Main-fonts/CircularSpotifyText-Bold.otf);
}

@font-face {
    font-family: "Main-Book";
    src: url(/src/Assets/Fonts/Main-fonts/CircularSpotifyText-Book.otf);
}

.Banner-cta {
    min-width: 328px;
    height: 234px;

}

.Wrapper-banner-cta {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
}

/*  */
.Heading-top-Banner {
    margin: 0px 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

/* Heading Text */
.Top-new-sect div:nth-child(1) {
    user-select: none;
    display: flex;
    flex-direction: column;
}

.Heading-top-Banner h1 {
    font-size: 16px;
    font-family: 'Main-Bold', sans-serif;
    margin-bottom: 6px;
}

.Heading-top-Banner p {
    font-size: 12px;
    font-family: 'Main-Book', sans-serif;
    
}

/* Button section */
.Heading-top-Banner div:nth-child(2) button {
    width: 80px;
    height: 25px;
    border-radius: 4px;
    border: none;
    font-size: 12px;
    font-family: 'Main-Bold', sans-serif;
    color: var(--Ground-White);
    background-color: var(--Accent-color-btn);
    margin-top: 12px;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 4px 8px;
}

.Heading-top-Banner div:nth-child(2) button img {
    border-left: 1px solid white;
    padding-left: 8px;
}

/* ?Banner Cta */
.Banner-home-cta {
    min-width: 328px;
    height: 148px;
    margin-top: 14px;
    border-radius: 8px;
}



.ImageStyle {
    margin: "0px 16px";
    min-Width: "328px";
    height: "148px";
}


/* Comming soon poup */
.Comingsoon-continer {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: fixed;
    z-index: 120;

    display: flex;
    align-items: center;
    justify-content: center;
    animation: fadeing 0.5s ease;
}

@keyframes fadeing{
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}



.black-background {
    width: 100%;
    height: 100%;
    background-Color: #00000050;
}

.main-comingsoon {
    position: absolute;
    width: 260px;
    height: 240px;
    background-color: var(--Ground-White);
    border-radius: 16px;
    padding: 16px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.button-coming {
    width: 80px;
    height: 25px;
    border: none;
    border-radius: 4px;
    color: white;
    font-family: inter;
    font-size: 12px;
    cursor: pointer;
    background-color: var(--Accent-color-btn);
}