/* Import Font */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

::-webkit-scrollbar {
  width: 4px;
  height: 4px;
  background-color: var(--Ground-Black);
}

::-webkit-scrollbar-thumb {
  background-color: var(--Inner-Input);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* Mobile, remove Blue highlight if click */
  -webkit-tap-highlight-color: transparent;
}

body {
  background-color: var(--Shade-box);
}
@font-face {
  font-family: "Main-Medium";
  src: url(./Assets/Fonts/Main-fonts/CircularSpotifyText-Medium.otf);
}
@font-face {
  font-family: "Main-Book";
  src: url(./Assets/Fonts/Main-fonts/CircularSpotifyText-Book.otf);
}

/* COLOR BOX */
:root {
  --Ground-White: #FCFCFC;
  --Ground-Black: #111111;
}

:root {
  --Background-cta-btn: #FF634726;

}

:root {
  --Line-universal: #E8EFF3;
  --Gray-color-universal: #D9D9D9;
}

/* Offline indicators */
.Offline-indicators {
  width: 100vw;
  height: 100vh;
  background-color: red;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: fixed;
}


.App {
  /* max-width: 405px; */
  width: 360px;
  /* height: 265vh; */
  margin: auto;
  background-color: var(--Ground-White);
}

#Home {
  width: 100%;
  height: 100%;
  background-color: var(--Ground-White);
}

.Header {
  position: sticky;
  top: 0px;
  height: 110px;
  width: 100%;
  background-color: var(--Ground-Black);
  z-index: 100;
}

.Wrapper-H {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: end;
  padding: 16px;
}

/* *Search Boc {Header} */
:root {
  --Shade-box: #2A2A2A;
  --Inner-Input: #999999;
}

.Search-Box {
  min-width: 267px;
  height: 45px;
  padding: 0px 12px;
  border-radius: 8px;
  gap: 12px;
  
  display: flex;
  align-items: center;
}

.Search-Box input {
  background-color: transparent;
  border: none;
  font-family: 'inter', sans-serif;
  font-weight: 400;
  color: var(--Inner-Input);
  height: 100%;
  width: 100%;
}

.Search-Box input:focus {
  outline: none;
  border: none;
}

.Setting-Section {
  cursor: pointer;
  width: 45px;
  height: 45px;
  background-color: var(--Shade-box);
  border-radius: 8px;

  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 16px;
  text-decoration: none;
}

.Output-Input {
  position: absolute;
  min-width: 360px;
  overflow-y: scroll;
  color: white;
  background-color: var(--Ground-Black);
  font-size: 12px;
}

.Wrapper-output-input {
  padding: 4px 24px;
}