* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.Long-card-pilihan {
    padding: 0px 16px;

    margin-top: 24px;
    min-width: 328px;
    height: 330px;
}

.Top-pilihan-sect {
    width: 100%;
    height: fit-content;
    margin-bottom: 6px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.Top-pilihan-sect h1 {
    font-size: 16px;
    font-family: 'Main-Bold', sans-serif;
}

.Top-pilihan-sect > div button {
    width: 80px;
    height: 25px;
    border-radius: 4px;
    border: none;
    font-size: 12px;
    font-family: 'Main-Bold', sans-serif;
    color: var(--Btn-color);
    background-color: var(--Background-cta-btn);
    ;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;
}

/* Content Section  */
.Content-long-RP {
    height: 300px;
}

.Wrapper-content-long-RP {
    width: 100%;
    height: 100%;
    overflow-x: scroll;
    
    display: flex;
    flex-direction: row;
    gap: 8px;
}

.Wrapper-content-long-RP::-webkit-scrollbar {
    display: none;
}