* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Main Color for nav */
:root {
    --Accent-color-nav: #646464;
}

/* COLOR BOX */
:root {
    --Ground-White: #FCFCFC;
    --Ground-Black: #111111;
}

/* *Search Boc {Header} */
:root {
    --Shade-box: #2A2A2A;
    --Inner-Input: #999999;
}

:root {
    --Line-universal: #E8EFF3;
}

/* .active {
    background-color: #11111125;
} */

/* Bottom Nav */
.Bottom-nav {
    z-index: 50;
    position: fixed;
    bottom: 0;
    height: 52px;
    min-width: 360px;
    
}

.Wrapper-bottom-nav {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* gap: 50px; */
    padding: 8px 32px;
}

/* Home cta */
.Bottom-nav-HomeCta {
    width: 50px;
    height: 50px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    user-select: none;
    cursor: pointer;
    text-decoration: none;
}

.Bottom-nav-HomeCta p {
    font-family: 'inter', sans-serif;
    font-weight: 600;
    color: var(--Accent-color-nav);
    font-size: 9px;
    text-transform: uppercase;
}

